
import Comments from '../Components/CommentsLists'
export default function Post({post}){
    const {id,title,body} = post

    return(
    <>
        <table>
            <tr>
                <td>ID :</td>
                <td>{id}</td>
            </tr>
            <tr>
                <td>Title :</td>
                <td>{title}</td>
            </tr>
            <tr>
                <td>Body :</td>
                <td>{body}</td>
            </tr>
        </table>
        <Comments id={id}>

        </Comments>
        
</>
    )
}